<template>
	<div>
		<v-card max-width="350px" class="pa-4">
								<h1 class="text-center">Create An Account</h1>
								<v-text-field v-model="email" type="email" label="Email Address" color="rgba(11,0,80,140)"></v-text-field>
								<v-text-field v-model="username" label="Username" color="rgba(11,0,80,140)"></v-text-field>
								<v-text-field v-model="password" type="password" label="Password" color="rgba(11,0,80,140)"></v-text-field>
								<div class="d-flex justify-center"><v-progress-circular color="rgba(11,0,80,140)" v-show="progress" indeterminate></v-progress-circular></div>
								<div class="d-flex justify-center"><v-alert :type="type" v-show="alert">{{message}}</v-alert></div>
								<div class="d-flex justify-center"><v-btn @click.stop="register" color="rgba(11,0,80,140)" dark rounded large>Sign-Up</v-btn></div>
							</v-card>
	</div>
</template>
<script>
	export default {
		name:'Register',
		data(){
			return {
				email:'',
				username:'',
				password:'',
				alert:false,
				progress:false,
				type:undefined,//success,error,warning,info
				message:'',
			}
		},
		methods:{
			register(){
				this.alert=false;
				this.progress=true;
				let form=new FormData();
				form.append('email',this.email);form.append('password',this.password);form.append('username',this.username);
				this.axios({
					url:this.$store.getters.getApi+'register',
					method:'POST',
					data:form,
					headers:{'Access-Control-Allow-Origin':'*'}
				})
				.then((response)=>{
					let data = response.data;
					if(data.error){
						this.alert=true;
						this.message=data.message;
						this.type="error"
				this.progress=false;
					}
					else{
						this.alert=true;
						this.message=data.message;
						this.type="success"
				this.progress=false;
					}
				})
				.catch((error)=>{
					if(error){
						this.alert=true;
						this.message="Network Error";
						this.type="error"
				this.progress=false;
					}
				})
			}
		}
	}
</script>