<template>
	<div>
		<v-card max-width="350px" class="pa-4">
								<h1 class="text-center">Login To Account</h1>
								<v-text-field v-model="email" type="email" label="Email Address" color="rgba(11,0,80,140)"></v-text-field>
								
								<v-text-field v-model="password" type="password" label="Password" color="rgba(11,0,80,140)"></v-text-field>
								<div class="d-flex justify-center"><v-progress-circular color="rgba(11,0,80,140)" v-show="progress" indeterminate></v-progress-circular></div>
								<div class="d-flex justify-center"><v-alert :type="type" v-show="alert" v-html="message"></v-alert></div>
								<div><v-btn v-show="if_logged_in_prev" @click.stop='loginHere' color="red" dark>Login Here</v-btn></div>
								<div class="d-flex justify-center mt-1"><v-btn @click.stop="login" color="rgba(11,0,80,140)" dark rounded large>Sign-In</v-btn></div>
							</v-card>
	</div>
</template>
<script>
	export default {
		name:'Login',
		data(){
			return {
				email:'',
				password:'',
				alert:false,
				progress:false,
				type:undefined,//success,error,warning,info
				message:'',
				//TEMP LOGIN DATA
				user:{},
				token:'',
				if_logged_in_prev:false,
				tempLogData:{}
			}
		},
		methods:{
			login(){
				this.if_logged_in_prev=false;
				this.alert=false;
				this.progress=true;
				let form=new FormData();
				form.append('email',this.email);form.append('password',this.password);
				this.axios({
					url:this.$store.getters.getApi+'login',
					method:'POST',
					data:form,
					headers:{'Access-Control-Allow-Origin':'*'}
				})
				.then((response)=>{
					let data = response.data;
					if(!data.error)
					{
						this.progress=false;
				this.message=data.message+' please wait...';
				this.alert=true;
				this.type='success';
				this.token=data.token;
				this.user=data.user;
				this.tempLogData=this.user;
				this.$socket.emit('checkConnection2',{client:data.user.id , sender:data.user.id});
				//this.$store.commit('setApiToken',data.token);
				//this.$store.commit('setUser',data.user);
				//this.$router.push('/dashboard');
					}
					else{
						this.progress=false;
						this.alert=true;
						alert(data.message)
				this.message="Network Error";
				this.type='error';
					}
				})
				.catch((error)=>{
					if(error){
						this.alert=true;
				this.progress=false;
				this.message="Network";
				this.type='error';
					}
				});
			},
			finalLogin(){
				this.$store.commit('setApiToken',this.token);
				this.$store.commit('setUser',this.user);
				this.$router.push('/dashboard');
			},
			loginHere(){
				this.user.id=this.tempLogData.id;
				this.$socket.emit('checkConnection2_previous_remove',{client:this.tempLogData.id , sender:this.tempLogData.id});
			},
		},
		sockets:{
			connectionFeedback2:function(data){//AFTER CHECK IF WHERE ALREADY ONLINE
				//alert('Message for '+data.client+'me '+this.user.id)
				if(data.state && data.client===parseInt(this.user.id)){
					this.message="You are logged in from another device!";
					this.if_logged_in_prev=true;
				}
				else{
				this.finalLogin();
				}
			}
		}
	}
</script>